<template>
  <!-- 菜单 -->
  <div class="main_con_box">
    <div class="menu_top_btn">
      <el-button type="primary" size="small" @click="onSubmit()"
        >新 建</el-button
      >
    </div>
    <div class="menu_main">
      <el-table
        :data="state.tableData"
        border
        style="width: 100%"
        :height="$store.state.innerheight - 165"
        size="mini"
        :header-cell-style="{background:'#f5f5f5'}"
      >
        <el-table-column fixed type="index" label="序号" width="80">
        </el-table-column>
        <el-table-column prop="menu_name" label="菜单名称" width="">
        </el-table-column>
        <el-table-column prop="route" label="路径" width=""> </el-table-column>
        <el-table-column prop="identification" label="标识" width="">
        </el-table-column>
        <el-table-column prop="remarks" label="备注" width="">
        </el-table-column>
        <el-table-column label="操作" width="">
          <template #default="scope">
            <el-button @click="handleEdit(scope.row)" type="text" size="mini"
              >编辑</el-button
            >
            <el-button type="text" style="color:red" size="mini" @click="handleDel(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="page">
        <el-pagination
          v-model:currentPage="currentPage4"
          :page-sizes="[20, 50, 70, 100]"
          :page-size="20"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        >
        </el-pagination>
      </div>
  </div>
  <div>
    <detail ref="details"></detail>
  </div>
</template>

<script>
import { reactive } from "vue";
import detail from "../menu/menu_details.vue";
import { getCurrentInstance } from "@vue/runtime-core";
export default {
  components: {
    detail
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      tableData: [
        {
          menu_name: "1",
          route: "1",
          identification: "1",
          remarks: "1",
        },
        {
          menu_name: "1",
          route: "1",
          identification: "1",
          remarks: "1",
        },
        {
          menu_name: "1",
          route: "1",
          identification: "1",
          remarks: "1",
        },
      ],
    });
    const onSubmit = () => {
      console.log(proxy.$refs.details);
      proxy.$refs.details.setshow();
    };
    const handleEdit = (res) => {
      proxy.$refs.details.setedit(res);
    };
    const handleDel = () => {
      console.log();
    };
    const handleSizeChange = (val) => {
      console.log(`${val} items per page`);
    };
    const handleCurrentChange = (val) => {
      console.log(`current page: ${val}`);
    };

    return {
      state,
      onSubmit,
      handleEdit,
      handleDel,
      handleSizeChange,
      handleCurrentChange
    };
  },
};
</script>

<style scoped>
.main_con_box {
  background-color: #fff;
  padding: 10px;
  box-sizing: border-box;
}
.menu_top_btn {
  text-align: right;
}
.menu_main {
  margin-top: 10px;
}

</style>
