<template>
  <div class="container">
    <el-dialog title="菜单信息" v-model="dialogVisible" width="560px">
      <div>
        <div class="menu_details_line">
          <span>序号</span>
          <el-input v-model="input" size="mini" placeholder=""></el-input>
        </div>
        <div class="menu_details_line">
          <span>菜单名称</span>
          <el-input v-model="input2" size="mini" placeholder=""></el-input>
        </div>
        <div class="menu_details_line">
          <span>路径</span>
          <el-input v-model="input3" size="mini" placeholder=""></el-input>
        </div>
        <div class="menu_details_line">
          <span>标识</span>
          <el-input v-model="input4" size="mini" placeholder=""></el-input>
        </div>
        <div class="menu_details_line">
          <span>父级菜单</span>
          <el-select
            size="mini"
            style="width: 100%"
            v-model="menuId"
            placeholder="请选择"
          >
            <el-option
              v-for="item in menu_list"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div class="menu_details_line">
          <span>备注</span>
          <el-input
            type="textarea"
            :rows="1"
            v-model="input5"
            size="mini"
            placeholder=""
          ></el-input>
        </div>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button size="mini" type="primary" @click="dialogVisible = false"
            >保 存</el-button
          >
          <el-button size="mini" @click="dialogVisible = false"
            >取 消</el-button
          >
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { reactive, toRefs } from "@vue/reactivity";
import { getCurrentInstance } from "@vue/runtime-core";

export default {
  setup() {
    const state = reactive({
      menuId: "",
      menu_list: [],
      dialogVisible: false,
      input: "",
      input2: "",
      input3: "",
      input4: "",
      input5: "",
    });
    const { proxy } = getCurrentInstance();
    const setshow = () => {
      proxy.dialogVisible = true;
    };
    const setedit = () => {
      proxy.dialogVisible = true;
    };
    return {
      setshow,
      setedit,
      ...toRefs(state),
    };
  },
};
</script>
<style scoped>
.menu_details_line {
  display: flex;
  align-items: center;
  margin-top: 10px;
  width: 490px;
}

.menu_details_line > span {
  width: 100px;
  text-align: right;
  margin-right: 5px;
}
</style>
